@import 'config';
@import 'menu';
@import 'about';
@import 'work';


* {
  box-sizing: border-box;
}

body {
  //@include background();
  background: url(../img/laptop.jpg);
  background-attachment: fixed;
  background-size: cover;
  color: #fff;
  height: 100%;
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.5;
}
#root {
  width: 100%;
  height: 100%;
  z-index: -1;
  background: rgba($primary-color, $background-opacity);
}

 
//Headings
h1,
h2, 
h3 {
  margin: 0;
  font-weight: 400;

  &.lg-heading {
    font-size: 6rem;
  }

  &.sm-heading {
    margin-bottom: 2rem;
    padding: 0.2rem 1rem;
    background: rgba(lighten(#006F48, 2), 0.2);
  }
}

a {
  color: #fff;
  text-decoration: none;
}

header {
  position: fixed;
  z-index: 2;
  width: 100%;
}

.text-secondary {
  color: $secondary-color;
}

main {
  padding: 4rem;
  min-height: calc(100vh - 60px);//чтобы футер прилипал к нижней границе экрана, считаем высоту блока над ним - мэйн, он должен быть "высота экрана(100vh) минус высота футера(60px)"

  .icons {
    margin-top: 1rem;

    a {
      padding: 0.4rem;

      &:hover {
        color: $secondary-color;
        @include easeOut()
      }
    }
  }

  &#home {
    overflow: hidden;
    h1 {
      margin-top: 30vh;
    }
  }
}







